import React from 'react'
import {Link, Redirect} from 'react-router-dom'
import './HomePage.scss'
import $ from 'jquery'
import QRcode from 'qrcode.react'

// 
import {urlImg , checkReceive , getUserInfomationr , userAssisted , receiveGiftOrder , ActiveName} from './../apis/api'

// 
import gotten from './images/gotten.png'
import close from './images/Close.png'
import HPHeader_Tit1 from './images/HPHeader_Tit1.svg'
import HPUserInfo_Tit from './images/HPUserInfo_Tit.png'
import unLogin from './images/UnLogin.png'
import HPUserInfo_Bck1 from './images/HPUserInfo_Bck1.png'
import HPUserInfo_Button from './images/HPUserInfo_Button.png'
import HPProduct_Img from './images/HPProduct_Img.png'
import HPActStep_Item1 from './images/HPActStep_Item1.png'
import HPActStep_Item2 from './images/HPActStep_Item2.png'
import BotLogo from './images/BotLogo.png'
import saveImg from './images/saveImg.png'
import getAct from './images/getAct.svg'
import ForHeActSuc from './images/ForHeActSuc.svg'


// 
import Loading from '../Components/Loading/Loading';
import Tips from '../Components/Tips/Tips'
import WhereOpen from '../Components/WhereOpen/WhereOpen'

//
import {getBase64,SAVELOCAL} from './../Methods/Methods'


class HomePage extends React.Component{
    constructor(props){
        super(props)
        this.state={
            avatar : '',
            nickname : '',
            url : '',
            count : 0,
            isLogin : false,
            liked : false,
            isReceive : false,// F T F
            isHidden :false,
            isOrder : false, // F T F
            isAct : true,// F T T
            isActPop : false, // F T F
            isActSuc : false // F T F
        }

        this.default = this.default.bind(this)//
        this.goLogin = this.goLogin.bind(this)
        this.saveImg = this.saveImg.bind(this)
        this.Act = this.Act.bind(this)
        this.SucAct = this.SucAct.bind(this)
        this.creactImg = this.creactImg.bind(this)
        this.goPay = this.goPay.bind(this) //
        this.CloseActPop = this.CloseActPop.bind(this)//
        this.ForHeAct = this.ForHeAct.bind(this) //
        this.CloseActSuc = this.CloseActSuc.bind(this)//
        this.IMJoin = this.IMJoin.bind(this) //
        this.saveCode = this.saveCode.bind(this)//
        this.getOrder = this.getOrder.bind(this) //
        this.CloseGetOrder = this.CloseGetOrder.bind(this) //
        
    }
    componentWillUnmount(){
        if(this.getUserInfomationr){
            this.getUserInfomationr.abort()
        }
    }
    componentDidMount(){
        var key = window.location.hash.substr(3),
            url = '/' + this.props.history.location.hash,
            storage = window.localStorage;
        var be_mid = storage.be_mid;
        console.log(key.indexOf('@')>= 0)

        if(key){
            if(key.indexOf('@')>= 0){
                console.log('')
                console.log(key.replace('@',''))
                var keys = key.replace('@','')
                this.setState({
                    isLogin : true
                })
                this.refs.childRef.refs.Loading.style.display = "block"
                var _this = this
                $.get(getUserInfomationr , {keys : keys} , function(res){
                    if(res.msg == "SUCCESS"){
                        _this.refs.childRef.refs.Loading.style.display = "none"
                        _this.setState({
                            count : res.data.count,
                            // count : 1,
                            midBase : keys,
                            avatar : res.data.avatar,
                            nickname : res.data.nickname,
                        })
                        SAVELOCAL('mid',res.data.mid)
                        _this.checkReceive(res.data.mid)
                    }
                })
            }
            else{
                console.log('')
                SAVELOCAL('ActPath',url)
                if(be_mid){
                    console.log('')
                    storage.removeItem("ActPath")
                    this.refs.childRef.refs.Loading.style.display = "block"
                    this.getKeyInfo(key)
                    this.setState({
                        key : key,
                        isYou:true,
                        isLogin : true,
                        isHidden : true,
                        isActPop : true
                    })
                }else{
                    this.props.history.push('/Login')
                }
            }
            
        }
        else{
            console.log('')
            this.isLogin()
            // this.getAvaAndId()
            this.setState({
                isMe : true
            })
        }
    }


    
    //
    default(){
        return false
    }
    //
    // getAvaAndId(){
    //     var storage = window.localStorage,
    //         avatar = storage.avatar,
    //         nickname = storage.nickname;
    //     console.log(avatar)
    //     console.log(nickname)
    //     if(!avatar || !nickname){
    //         console.log('!!!')
    //     }else{
    //         this.setState({
    //             avatar : avatar,
    //             nickname : nickname,
    //         })
    //     }
    // }
    // key
    getKeyInfo(key){
        var _this = this
        if(key){
            this.getUserInfomationr = $.get(getUserInfomationr,{keys:key},function(res){
                if(res.msg === 'SUCCESS'){
                    console.log(res)
                    _this.setState({
                        count : res.data.count,
                        // count:2,
                        avatar : res.data.avatar,
                        nickname : res.data.nickname
                    })
                    _this.isKeyCount()
                    _this.refs.childRef.refs.Loading.style.display = "none"
                    
                }
                else{
                    console.log('error')
                    var This = _this
                    _this.setState({
                        color:'red',
                        TipsInfo : ''
                    })
                    _this.refs.childRefTwo.refs.Tips.style.top = '0%'
                    _this.timerItem2 = setTimeout(function(){
                        This.refs.childRefTwo.refs.Tips.style.top = '-40%'
                    },3000)
                }
            })
        }
        else{
            console.log('error')
        }
    }
    //cont
    isKeyCount(){
        var count = this.state.count,
            _this = this
        if(count >= 2){
            this.setState({
                isAct : false,
                isActPop : false,
                isActSuc : false,
                isYou : true,
                TipsInfo : "!"
            })
            this.timerItem1 = setTimeout(function(){
                _this.refs.childRefTwo.refs.Tips.style.top = '0%'
            },1000)
            this.timerItem2 = setTimeout(function(){
                _this.refs.childRefTwo.refs.Tips.style.top = '-40%'
            },3000)
        }
    }

    //    
    //
    isLogin(){
        var storage = window.localStorage,
            keys = storage.keys;
            
        if(keys){
            this.refs.childRef.refs.Loading.style.display = "block"
            console.log('is Login')
            this.setState({
                isLogin : true
            })
            var _this = this
            $.get(getUserInfomationr , {keys : keys} , function(res){
                console.log(res)
                if(res.msg == "SUCCESS"){
                    _this.refs.childRef.refs.Loading.style.display = "none"
                    _this.setState({
                        count : res.data.count,
                        // count : 2,
                        midBase : keys,
                        avatar : res.data.avatar,
                        nickname : res.data.nickname,
                    })  
                        SAVELOCAL('mid',res.data.mid)
                        _this.isCountTWO()
                    
                    
                    var codeUrl = window.location.href +  "#" + _this.state.midBase,
                        mid = res.data.mid;
                    _this.checkReceive(mid)
                    _this.setState({
                        url : codeUrl
                    })
                }
            })
        }
        else{
            console.log('no Login')
        }    
    }

    // 
    checkReceive(mid){
        console.log(mid)
        var _this = this
        $.get(checkReceive , {mid:mid},function(res){
            console.log(res)
            if(res.data){
                _this.setState({
                    isReceive : true
                })
            }
        })
    }
    
    //
    isCountTWO(){
        var count = this.state.count
        console.log(count)
        if(count >= 2){
            console.log('Two')
            this.setState({
                isMe : false,
            })
        }
    }


    
    
    


    // *************
        // 
        goLogin(){
            this.props.history.push('/Login')
        }
        //
        goPay(){
            var storage = window.localStorage;
            var mid = storage.mid;
            if(mid){
                this.props.history.push('/postage')
            }
            else{
                this.props.history.push('/Login')
            }
        }
        //
        saveImg(){
            console.log('')
            this.setState({
                liked : true
            })
        }
        //
        saveCode(){
            var _this = this
            this.setState({
                TipsInfo : ''
            })
            this.refs.childRefTwo.refs.Tips.style.top = '0%'
            
            this.timerItem2 = setTimeout(function(){
                _this.refs.childRefTwo.refs.Tips.style.top = '-40%'
            },3000)
        }
        //
        getOrder(){
            console.log('')
            this.setState({
                isOrder : true
            })
        }
        //
        CloseGetOrder(){
            this.setState({
                isOrder : false
            })
        }
        // 
        creactImg(e){
            var mycansrc= $('#qrcode canvas')[0]; 
            var canvas =this.refs.creactCanvas;

            $("body,html").scrollTop(0);
            $("body").css("overflow","hidden")

            // baseData:""  /base64
            var _this = this

            this.setState({
                isClose : true
            })
            
            this.refs.codeWrap.style.display = "block"
            // console.log(this.refs.childRef.refs.Loading);
            this.refs.childRef.refs.Loading.style.display = "block"
            this.refs.SaveImgBtn.style.display = "block"
            

            getBase64(mycansrc,canvas,urlImg);

            setTimeout(function(){
                _this.refs.code.style.display = 'block'
                var baseData =   sessionStorage.getItem("baseData");
                _this.refs.childRef.refs.Loading.style.display = "none"
                _this.refs.Closecode.style.display = "block"
                // console.log(baseData)
                _this.setState({
                    isClose : false,
                    baseData:baseData
                })
            },5000)
        }

        nocloseCode(){

        }
        closeCode(){
            $("body").css("overflow","visible")
            this.refs.codeWrap.style.display = "none"
            this.refs.code.style.display="none"
            this.refs.SaveImgBtn.style.display="none"
        }
        //
        SucAct(){
            var count = this.state.count,
                _this = this
            if(count >= 2){
                this.receiveGiftOrder()
            }
            else{
                console.log('!!!')
                this.setState({
                    TipsInfo : '',
                    color:'red'
                })
                this.refs.childRefTwo.refs.Tips.style.top = '0%'
                
                this.timerItem2 = setTimeout(function(){
                    _this.refs.childRefTwo.refs.Tips.style.top = '-40%'
                },3000)
            }
            
        }
        //
        receiveGiftOrder(){
            var storage = window.localStorage;
            var mid = storage.mid,
                _this = this
            $.ajax({
                url: receiveGiftOrder,
                type:'get',
                data:{
                    mid : mid,
                    name : ActiveName,
                    type : 0
                },
                dataType: 'json',
                contentType: "application/x-www-form-urlencoded",
                success(res){
                    console.log(res)
                    if(res.msg === ''){
                        console.log('')
                    }
                    else if(res.msg === ''){
                        console.log('')
                    }
                    else{
                        _this.props.history.push('/ActSuc')
                    }
                },
                fail(){
                    console.log('error')
                }

            })
        }

        close(){
            this.setState({
                liked : false
            })
        }

    //  **************
        //
        Act(){
            //
            if(this.state.isAct){
                this.setState({
                    isActPop : true
                })

            }else{ //
                this.IMJoin()
            }
        }
        //
        IMJoin(){
            this.refs.ActBtn.innerHTML = ""
            console.log('join')
            console.log(window.location.reload())
            // window.opener.location.reload()
            this.props.history.push('')
        }
        //
        ForHeAct(){
            this.userAssisted()
        }

        // 
        userAssisted(){
            var key = window.location.hash.substr(3);
            var storge = window.localStorage;
            var be_mid = storge.be_mid,
                _this = this,
                url = '/' + this.props.history.location.hash;
            $.get(userAssisted,{be_mid : be_mid , keys : key},function(res){
                console.log(res)
                if(res.msg === ''){
                    console.log('error')
                    SAVELOCAL('ActPath',url)
                    _this.props.history.push('/Login')
                }else{
                    console.log('ok')
                    _this.setState({
                        isActPop:false,
                        isActSuc : true,
                        isAct : false,
                        count : _this.state.count+1
                    })
                }
            })
        }
        //
        CloseActPop(){
            this.setState({
                isActPop : false
            })
        }
        //
        CloseActSuc(){
            this.setState({
                isActSuc : false
            })
        }



    render(){
        return(
            <div className="HPWrap">

                <div className="HPMain">

                    <WhereOpen></WhereOpen>
                    <Loading ref="childRef"></Loading>
                    <Tips 
                        ref="childRefTwo"
                        info={this.state.TipsInfo}
                        color = {this.state.color}
                    ></Tips>
                
                {/**/}
                <header className="HP_HeaderWrap">

                        <div className="HP_HeaderTit1">
                            <span className="Hp_HeaderTit1Text">
                                ""
                            </span>
                        </div>

                        <div className="HP_HeaderTit2">
                            <img src={HPHeader_Tit1} width="100%"/>
                        </div>

                    </header>
                

                {/**/}
                <main className="HPUserInfo_Container">
                        
                        {/**/}
                            <div className="HPUserInfo_TitWrap">
                                <img src={HPUserInfo_Tit} width="100%"/>
                                <div className="HPUserInfo_AvaWrap">
                                    <img src={this.state.avatar || unLogin}/>
                                </div>
                                <p className="HPUserInfo_IdWrap">
                                    <span>{this.state.nickname}</span>
                                </p>
                            </div>
                        {/**/}
                            <div className="HPUserInfo_MainWrap">

                                {/**/}
                                <div className="HPUserInfo_Item1">
                                    <img src={HPUserInfo_Bck1} />
                                </div>

                                {/** */}
                                <div className="HPUserInfo_Item2">
                                    {/** */}
                                    <section className="HPUserInfo_MainTit">
                                        <span className="HPUserInfo_MainText">2</span>
                                        <div onClick={this.state.count >=2 ? this.SucAct : this.default} hidden={this.state.isHidden ? true : false} className="HPUserInfo_MainBtn" style={ this.state.count >=2 ? {'background' : 'rgb(252,216,38)'} : {'background' : 'rgb(128,128,128)'} }>
                                            <span style={ this.state.count >= 2 ? {'color' : '#000'} : {'color' : '#fff'} }></span>
                                        </div>
                                    </section>

                                    {/*  */}
                                    <section className="HPUserInfo_ProGWrap">
                                        <span className="HPUserInfo_ProItem1">
                                            <span className="HPUserInfo_ProItem2" style={ this.state.count === 0 ? {'width':`${0}%`} : this.state.count === 1 ? {'width' : `${50}%`} : {'width' : `${100}%`} }>
                                            </span>
                                        </span>

                                        <p className="HPUserInfo_Text1" style={{'left' : `${43}%`}}>1</p>
                                        <p className="HPUserInfo_Text2" style={{'right' : `${-5}%`}}>2</p>
                                    </section>
                                
                                    {/**  */}
                                    <section className="HPUserInfo_BtnWrap" style={this.state.isHidden ? {'marginTop':`${60}px`} : {'marginTop' : `${32}px`}}>
                                        <div hidden={this.state.isLogin} className="HPUserInfo_BtnLogin" onClick={this.goLogin}>
                                            <span></span>
                                        </div>

                                        <div className="HPUserInfo_isReceive" hidden={!this.state.isLogin} >
                                            <div hidden={this.state.isReceive} className="HPUserInfo_BtnLogin" 
                                                 onClick = {this.state.isYou ? this.Act : this.state.isMe ? this.saveImg : this.SucAct}
                                            >
                                                {/* <span></span> */}
                                                <span style={{'display' : this.state.isAct ? 'block' : 'none'}} ref="ActBtn">{this.state.isYou ? "" : this.state.isMe ? "" : ""}</span>
                                                <span style={{'display' : this.state.isAct ? 'none' : 'block'}}></span>
                                            </div>
                                            <div 
                                                hidden={!this.state.isReceive} 
                                                className="HPUserInfo_BtnLogin"
                                                onClick={this.getOrder}
                                            >
                                            <span>  </span>
                                            </div>
                                        </div>

                                        

                                    </section>
                                
                                    {/**  */}
                                    <section className="HPUserInfo_BckPayWrap" onClick={this.goPay} style={this.state.isHidden ? {'display' : 'none'} : {'display' : 'block'}}>
                                        {/* <Link to="/postage" > */}
                                            <span ></span>
                                        {/* </Link> */}
                                    </section>
                                </div>

                                {/** */}
                                <div className="HPUserInfo_Item3">
                                    <img src={HPUserInfo_Bck1} width="100%"/>
                                </div>
                            </div>
                    
                        {/** */}
                            <footer className="HPUserInfo_Bottom">
                                <img src={HPUserInfo_Button} width="100%" />
                            </footer>
                    </main>
                

                {/** */}
                <main className="HPProduct_Container">
                    {/** */}
                    <header className="HPProduct_TitWrap">
                        <span></span>
                        <div className="HPProduct_TitItem1">
                            <img src={HPActStep_Item1} width="100%" />
                        </div>
                        <div className="HPProduct_TitItem2">
                            <img src={HPActStep_Item2} width="100%" />
                        </div>
                        <img src={HPUserInfo_Tit}/>
                    </header>

                    {/** */}
                    <div className="HPProduct_MainWrap">
                        {/**/}
                        <div className="HPProduct_Item1">
                        </div>

                        <div className="HPProduct_Item2">
                        </div>

                        {/** */}
                        <div className="HPProduct_Item3">
                        </div>
                    </div>

                    {/*  */}
                    <footer className="HPProduct_Bottom">
                        <img src={HPUserInfo_Button} width="100%"/>
                    </footer>
                </main>
                

                {/*  */}
                <main className="HPActStep_Container">
                    {/*  */}
                    <header className="HPActStep_Header">
                        <span></span>
                        <div className="HPActStep_HeaderItem1">
                            <img src={HPActStep_Item1} width="100%"/>
                        </div>
                        <div className="HPActStep_HeaderItem2">
                            <img src={HPActStep_Item2} width="100%"/>
                        </div>
                    </header>
                
                    {/*  */}
                    <section className="HPActStep_NewUserWrap">
                        <span className="CriItem1"></span>
                        <span className="CriItem2"></span>
                        {/*  */}
                        <p className="HPActStep_NewUserTit">
                            
                        </p>

                        {/*  */}
                        <ul className="HPActStep_NewUserMain">
                            <li>1.</li>
                            <li>2.</li>
                            <li>3.</li>
                            <li>4.</li>
                        </ul>
                    </section>


                    {/*  */}
                    <section className="HPActStep_OldUserWrap">
                        <span className="CriItem1"></span>
                        <span className="CriItem2"></span>
                        {/*  */}
                        <p className="HPActStep_NewUserTit">
                            
                        </p>

                        {/*  */}
                        <ul className="HPActStep_NewUserMain">
                            <li>1.</li>
                            <li>2.</li>
                            <li>3.</li>
                            <li>4.</li>
                            <li>5.</li>
                        </ul>
                    </section>



                </main>
                
                {/*  */}
                <main className="HPActRuler_Container">
                    {/*  */}
                    <header className="HPActRuler_Header">
                        <span></span>
                        <div className="HPActRuler_HeaderItem1">
                            <img src={HPActStep_Item1} width="100%"/>
                        </div>
                        <div className="HPActRuler_HeaderItem2">
                            <img src={HPActStep_Item2} width="100%"/>
                        </div>
                    </header>

                    {/*  */}
                    <main className="HPActRuler_MainWrap">
                        <ul className="HPActRuler_Main">
                            <li>1.</li>
                            <li>2.</li>
                            <li>3.</li>
                            <li>4.1</li>
                        </ul>
                    </main>
                </main>
               
               

                {/*  */}
                <footer className="HPBotLogo_Container">
                    <img src={BotLogo} width="100%"/>
                </footer>




                {/*  */}

                    {/*  */}
                    <div className="actPopupModle" style={{'display': this.state.isActPop ? 'block' : 'none'}} onClick={this.CloseActPop}></div>
                    {/*  */}
                    <div className="actPopup_wrap" style={{'display': this.state.isActPop ? 'block' : 'none'}} >
                        <div className="actPopup_main">
                            <img src={getAct} width="100%"/>
                            <div className="actPopup_Tit">
                                <span></span>
                            </div>
                            <div className="actPopup_Info">
                                <span> 2 </span>
                            </div>
                            <div className="actPopup_BtnWrap">
                                <div className="actPopup_Btn" onClick={this.ForHeAct}>
                                    <span>ta</span>
                                </div>
                            </div>

                        </div>

                        <div className="actPopup_Clo">
                            <img src={close} width="100%" onClick={this.CloseActPop}/>
                        </div>
                    </div>

                    {/*  */}
                    <div className="actSucModle" style={{'display': this.state.isActSuc ? 'block' : 'none'}} onClick={this.CloseActSuc}></div>
                    {/*  */}
                    <div className="actSuc_Wrap" style={{'display': this.state.isActSuc ? 'block' : 'none'}}>
                        <div className="actSuc_Main">
                            <img src={ForHeActSuc} width="100%"/>
                            <div className="actSuc_Tit">
                                <span>!</span>
                            </div>
                            <div className="actSuc_Info">
                                <span>(2,)</span>
                            </div>
                            <div className="actSuc_BtnWrap">
                                <div className="actSuc_Btn" onClick={this.IMJoin}>
                                <span></span>
                                </div>
                            </div>
                        </div>
                        <div className="actSuc_Clo">
                            <img src={close} width="100%" onClick={this.CloseActSuc}/>
                        </div>
                    </div>


                {/********** */}





                {/*  */}


                    {/**  */}
                    <div className="imgWrap" onClick={this.close.bind(this)} ref="saveImg" style={{'display' : this.state.liked == true ? 'block' : 'none'}}>
                    {/* <div className="imgWrap"  ref="saveImg" > */}
                        
                    </div>
                    {/** */}
                    <div className="saveImgWrap"style={{'display' : this.state.liked == true ? 'block' : 'none'}} >
                        {/* <div className="saveImg" onClick={this.creactImg.bind(this)}> */}
                        <div className="saveImg" style={{'display' : this.state.liked == true ? 'block' : 'none'}}>
                            <img src={saveImg} width="14%" onClick={this.creactImg} />
                            <span></span>
                        </div>
                    </div>


                    {/**  */}
                    <div className="codeWrap" ref="codeWrap"></div>
                    
                    {/** */}
                    <div className="saveCode_Wrap">
                        <div className="saveCodeWrap" ref="code">
                            <div className="closeCode" ref="Closecode">
                                <span onClick={this.state.isClose ? this.nocloseCode.bind(this) : this.closeCode.bind(this)}></span>
                            </div>
                            <div id="qrcode" style={{"display":"none"}}>
                                <QRcode value={this.state.url}></QRcode>
                            </div>
                            <img className="codeImg" src={this.state.baseData} style={{"display":this.state.baseData===""?"none":"block"}}/>
                            <canvas id="canvas" ref="creactCanvas" style={{"display":"none"}}>
                            </canvas>
                        </div>
                    </div>

                    {/*  */}
                    <div className="saveCode_BtnWrap" ref="SaveImgBtn">
                        <div className="saveCode_Btn" onClick={this.saveCode}>
                            <span></span>
                        </div>
                    </div>

                    {/*  */}
                    <div className="getOrderModel" hidden={this.state.isOrder ? false : true}></div>

                    {/*  */}
                    <div className="getOrder_Container" hidden={this.state.isOrder ? false : true}>
                        <div className="getOrder_Wrap">
                            <div className="getOrder_Tit">
                                <span></span>
                            </div>
                            <div className="getOrder_Main">
                                <span>TV</span>
                            </div>

                            <div className="getOrder_Btn">
                                <div className="getOrder_OK" onClick={this.CloseGetOrder}>
                                    <span></span>
                                </div>
                                <div className="getOrder_NO" onClick={this.CloseGetOrder}>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                    </div>

                {/************ */}

                

                </div>

                

                
                
                
            </div>
        )
    }
}









export default HomePage